nav {
	background-color: rgb(255, 255, 255);
	overflow: hidden;
	color: #9e9e9e;
	padding: 1em;
	border-bottom: rgb(255, 255, 255);
	font-family: 'Century Gothic',Futura,'URW Gothic L',sans-serif;
	}
.navbar {
	box-shadow: rgb(255, 255, 255);
}
nav ul a{
	    color: black;
}
.nav-item{
  background-repeat: no-repeat;
  background-size: 100% 0;
  background-position: 0 88%;
  -webkit-transition: background-size 0.25s ease-in;
  transition: background-size 0.25s ease-in;
}
.navbar.navbar-light .breadcrumb .nav-item .nav-link, .navbar.navbar-light .navbar-nav .nav-item:hover .nav-link:hover{
	color:rgb(250, 0, 0);
}
.nav-item:hover {
  background-size: 100% 100%;
  color: rgb(255, 0, 0);
}
.navbar-expand-md .navbar-collapse{
	display: inline-flex;
	align-items: center;
	justify-content: center;
}
.navbar-brand{
	align-items: center;
	justify-content: center 
}
	
nav.navbar{
	top: 0;
	width: 100%;
	left: 50%;
    margin-right: -50%;
	-webkit-transform: translate(-50%);
	        transform: translate(-50%);
	z-index: 100;
}

.menuDiv{
	height: 45px;
	margin: 2em;
}

	nav a {
		color: black;
	}

	nav a:visited {
		color: black;
	}
	.navbar{
		border-radius: 0;
		font-size: 90%;
		box-shadow: none;
	}
	

	.navWide {
		display: none;
		margin: 0 auto;
    }
		.wideDiv {
			text-align: center;
        }
			a {
				text-decoration: none;
				display: inline-block;
				padding: 0 2em;
			}
	

	
    .navNarrow i {
			float: left;
			cursor: pointer;
			color: #9e9e9e;
		}

		.navNarrow.narrowLinks {
            display: none;
        }

		.navNarrow a {
				text-decoration: none;
				display: block;
				float: left;
				clear: left;
				padding: 0.5em 0;
			}
			
@media (min-width: 600px){
	nav.navbar{
		position: fixed !important;
	}
}			
			
			  

@media (min-width: 480px) {
	
	nav {

	}
